.section3_2.container {
  ul {
    li.col-xs-6 {
      border-radius: 6px 0 0 0;
      width: 48%;
      display: block;
      text-align: center;
      font-size: 16px;
      padding: 0px;
      background-color: #f6f6f6;
      &:first-child {
        border-radius: 0 6px 0 0;
        margin-right: 4%; }
      a {
        vertical-align: middle;
        border: none;
        font-size: 16px;
        height: 65px;
        color: #7b7b7b; } } }
  padding: 0px;
  .tab-content {
    padding: 0px 17px 0px 17px;
    width: 100%;
    background-color: #eee; }
  .nav-tabs {
    border: none; }
  ol {
    padding: 0px;
    list-style-position: inside;
    li {
      font-size: 24px;
      font-weight: bold;
      img, p {
        width: 100%;
        font-size: 16px;
        font-weight: 400; } } }
  .nav.nav-tabs>li.active>a {
    color: black;
    font-weight: bold;
    background-color: #eee;
    &:focus, &:hover {
      background-color: #eee; } }

  .swap_mode {
    display: inline-block;
    vertical-align: middle;
    a {
      padding-bottom: 20px;
      img {
        width: 30px; }
      p {
        text-align: center; } } } }
